<template>
  <div class="bx" @mousewheel="mouseindex" @transitionend="mouseindexx">
    <div class="boxa" ref="boxs">
      <div class="releaseData" v-for="item in userOBJ" :key="item.id">
        <div class="box">
          <div class="title">{{ item.title }}</div>
          <div class="text">{{ item.text }}</div>
        </div>
      </div>
    </div>
    <div class="toTop">
      <div class="item">
        <button @click="tohome">回到首页</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "viewTwo",
  data() {
    return {
      //   索引
      indexd: 0,
      //   节流阀
      io: true,
    };
  },
  methods: {
    slide() {
      this.$refs.boxs.style.transform = `translate(0,${-this.indexd * 100}vh)`;
    },
    mouseindex(e) {
      if (this.io) {
        this.io = false;
        if (e.wheelDelta < 0) {
          // 判断是否向下滑动
          if (this.indexd === this.userOBJ.length - 1) {
            // 滚动到最后一屏时始终保持在最后一屏
            this.indexd -= 1;
            this.io = true;
          }
          this.indexd++;
        } else {
          if (this.indexd === 0) {
            this.indexd = 1;
            this.io = true;
          }
          this.indexd--;
        }
        this.slide(this.indexd);
      }
    },
    mouseindexx() {
      this.io = true;
    },
    tohome() {
      if (confirm("确认回到首页吗?")) {
        this.$router.push({
          name: "home",
        });
      }
    },
  },
  computed: {
    ...mapState({
      userOBJ: (state) => state.homeState.userinput,
    }),
  },
};
</script>

<style scoped>
.bx {
  overflow: hidden;
}
.boxa {
  width: 100vw;
  height: 100vh;
  transition: all 0.5s ease-in-out;
}
.releaseData {
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  padding: 70px;
  padding-right: 120px;
  border: 1px solid white;
  background-color: skyblue;
  overflow: hidden;
}
.box {
  width: 100%;
  height: 100%;
}
.title {
  width: 100%;
  height: 10%;
  color: white;
  font: 700 3rem "";
  border-bottom: 2px solid white;
  display: flex;
  align-items: flex-end;
}
.text {
  width: 100%;
  height: 85%;
  box-sizing: border-box;
  margin-top: 10px;
  padding: 20px 0;
  font: 1.5rem "";
  color: white;
  letter-spacing: 0.1em;
  border-bottom: 1px solid white;
  overflow: auto;
}
.toTop {
  position: fixed;
  top: 50vh;
  right: 2vw;
}
.toTop .item {
  width: 5rem;
  height: 3rem;
}
.toTop .item button {
  width: 100%;
  height: 100%;
  border: 0;
  outline: none;
  border-radius: 14px;
  user-select: none;
}
</style>
